@import '~antd/dist/antd.css';

@import '~codemirror/lib/codemirror.css';
@import '~codemirror/theme/material.css';

.app {
    margin-bottom: 50px;
}

.main {
    margin-bottom: 120px;
}

#main {
    height: 100%;
}

.react-codemirror2 {
    height: 100%;
}

.CodeMirror {
    height: 100%;
}

.finux-edit-codemirror {
    padding-top: 25px;
    height: 100%;
}
.finux-edit-main {
    height: 100%;
}

.finux-edit-title {
    position: fixed;
    width: 100%;
    height: 25px;
    padding-left: 15px;
    font-size: 15px;
    font-weight: bolder;
    color:#515151;

    box-shadow: 0px 5px 7px gray;
    border-style: none solid none;
    border-width: 1px;

}